.padding20
{
	padding: 40px;
}
.home-page
{
	.banner-img img
	{
		width: 55%;
		margin: 0 auto;
	}
	.banner-img
	{
		position: relative;
		width: 374px;
		height: 520px;
		margin: 0 auto;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.3);
		border-radius: 10px;
	}
	.banner-img img 
	{
		position: absolute;
		width: 100%;
		height: 100%;
		border-radius: 10px;
	}
	.banner-content
	{
		margin-top: 100px;
		h1
		{
			font-size: 60px;
		}
		p 
		{
			font-size: 24px;
			margin-bottom: 0px;
			margin-top: 0px;
			font-style: italic;
			line-height: 180%;
		}
	}
	.div1
	{
		.img img
		{
			width: 65%;
			margin-left: 17.50%;
			box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 25px 0px;
		}
		h4
		{
			margin-top: 0px;
			margin-bottom: 15px;
		}
		p 
		{
			margin-top: 2px;
			margin-bottom: 2px;
		}
		.row1
		{
			margin-top: 60px;
		}
		.icon-img 
		{
			position: relative;
			width: 50px;
			height: 50px;
			margin: 0 auto;
			margin-top: 25px;
		}
		.icon-img img
		{
			position: absolute;
			width: 100%;
			height: 100%;
		}
	}
	.custom-btn
	{
		border-radius: 30px;
	    padding: 8px 40px;
	    margin-top: 30px;
	    border: 2px solid #ec7a0a;
	    background-color: #f17400;
	    color: #ffffff;
	    text-transform: capitalize;
	    letter-spacing: 0.8px;
	    font-size: 20px;
	}
	.custom-btn:hover
	{
		background-color: #ffffff;
		color: #ec7a0a;
	}
	.width_70
	{
		width: 70%;
		margin: 0 auto;
	}
	.home-img
	{
		width: 180px;
		height: 180px;
		position: relative;
		margin: 0 auto;
	}
	.home-img img
	{
		position: absolute;
		width: 30%;
		height: 30%;
		padding: 70px;
	}
	.home-banner-view
	{
		margin-top: -305px;
	}
	.gray-bg
	{
		background-color: #f1f8fe;
	}
	.banner-icon1
	{
		position: absolute;
		opacity: 0.32;
		width: 100px;
	}
	.banner-icon2
	{
		position: absolute;
	    right: 112px;
	    opacity: 0.32;
	    width: 100px;
	}
	.banner-icon3
	{
		position: absolute;
	    left: 43%;
	    top: 34%;
	    opacity: 0.32;
	    width: 100px;
	}
	.template-type
	{
		padding: 20px;
		position: relative;
		margin: 0 auto;
	}
	.template-type img 
	{
		border: 2px solid transparent;
		border-radius: 4px;
	}
	.template-type img:hover
	{
		box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 25px 0px;
	}
	.margin-bottom50
	{
		margin-bottom: 50px;
	}
	.get-template
	{
		padding: 5px 40px;
		margin-top: 50px;
		letter-spacing: 0px;
	}
	
}
.btn-outline .MuiSvgIcon-root
{
  width: 16px;
}
.btn-outline
{
	padding: 0px !important;
	min-width: 30px !important;
	margin: 2px;
	margin-top: 15px;
}
.border-bottom
{
	border-bottom: 1px solid #f5f5f5;
}